/*
 *******************
 * PAGE MAIN STYLE *
 *******************
*/
html {
  font-family: 'Poppins', sans-serif;
}
body {
  background: $themeColor-Light;
}

/* HEADER */
h1 {
  font-size: 70px;
  margin: 0;
  padding: 0;
  font-weight: 400;
}
[role='presentation'] {
  cursor: pointer;
  font-weight: 600;
}
/* HEADER */
.display-2 {
  font-size: 60px;
  margin: 0;
  padding: 0;
  font-weight: 800;
}
h2 {
  font-size: 40px;
}
h3 {
  font-weight: 800;
  font-size: 30px;
  margin-bottom: 1em;
}
h4 {
  font-weight: 800;
  font-size: 20px;
  margin-bottom: 1em;
}
a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}
.header {
  padding: 38px 16px;
  background: #fff;
  &__wrapper {
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
    margin-bottom: 1em;
  }
  &__logo {
    margin-right: 1em;
  }
  &__nav li + li {
    margin-left: 1em;
  }
}
