.has--underline {
  position: relative;
  display: inline-block;
  margin-bottom: 24px;
  padding-bottom: 17px;
  &:after {
    content: '';
    left: 0;
    bottom: 0;
    position: absolute;
    height: 4px;
    width: 50%;
    display: block;
    margin: auto;
    background-color: $theme-yellow;
  }
}
section {
  padding-bottom: 100px;
  min-height: 700px;
  position: relative;
}
section {
  padding-top: 100px;
}
.about {
  &__text {
    max-width: 550px;
    font-size: 24px;
    padding: 1em;
    margin-left: auto;
    margin-right: auto;
    color: #4b4b4b;
  }
  &__img {
    margin: auto;
  }
}

.how-it-works {
  background-color: #f0f6f6;
  &__img {
    margin: auto;
    display: block;
    width: 100%;
  }
}
.why-it-works {
  padding-bottom: 0;
  background-color: $theme-teal;
  &__text {
    display: block;
    max-width: 450px;
    color: #fff;
    font-size: 24px;
    display: block;
    margin: auto;
    line-height: 1.4;
  }
  &__img {
    margin-left: auto;
    margin-right: auto;
    height: 520px;
    margin-top: 2em;
  }
}
.pricing {
  .grow-3 {
    width: 33.33%;
    padding: 1em;
  }
}
.pricing__section ~ section {
  padding-bottom: 30px;
}
.headline {
  font-weight: 800;
  position: relative;
  display: inline-block;
  margin-bottom: 1em;
  padding-bottom: 0.5em;
  &:after {
    content: '';
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    height: 4px;
    width: 100px;
    display: block;
    margin: auto;
  }
  &--teal {
    &:after {
      background-color: $theme-teal;
    }
  }
  &--yellow {
    color: #fff;
    &:after {
      background-color: $theme-yellow;
    }
  }
}
.pricing .headline--yellow {
  color: #000;
}
.subheadline {
  margin-bottom: 48px;
}
.teal--text {
  color: $theme-teal;
}
.yellow--text {
  color: $theme-yellow;
}
.teal--text {
  color: $theme-teal;
}
.grey--text {
  color: #a4a4a4;
}

.card {
  text-align: center;
  border-radius: 10px;
  background-color: $theme-teal;
  border: 1px solid #dbdbdb;
  font-weight: 800;
  max-width: 420px;
  margin: auto !important;

  &__title {
    min-height: 100px;
    background-color: $theme-yellow;
    display: flex;
    padding: 0.5em;
    align-items: center;
    justify-content: center;
    font-size: 26px;
  }
  &__price {
    color: #fff;
    font-size: 60px;
    padding: 0.75em;
    word-wrap: break-word;
  }
}

/* FOOTER */
.button {
  background-color: #fbfbfc;
  &:not(.button--yellow) {
    border: 1px solid #d8dce6;
  }
  &__wrapper {
    margin-top: 2em;
    margin-bottom: 2em;
  }
  border-radius: 25px;
  padding: 1em;
  display: inline-block;
  min-width: 200px;
  text-align: center;
  border: 0;
  &--yellow {
    background-color: $theme-yellow;
    color: black;
    padding: 1rem;
    font-size: 1rem;
    margin-bottom: 0.6rem;
  }
  &--teal {
    background-color: $theme-teal;
    color: #fff;
  }
  &--google {
    background-color: #dd4b39;
    color: #fff;
  }
  &--fb {
    background-color: #2174ee;
    color: #fff;
  }
  &__sign-up {
    margin-left: 1em;
  }
  &--large {
    padding: 1em 2em;
    min-width: 300px;
    font-size: 24px;
    border-radius: 50px;
    font-weight: 800;
  }
}
.footer {
  position: relative;
  &__title {
    margin-bottom: 1.5em;
    color: $theme-yellow;
    font-weight: 800;
  }
  &__links {
    font-size: 14px;
    li + li {
      margin-top: 1.25em;
    }
  }
  &__sign-up {
    background-color: $theme-yellow;
    padding: 48px 0;
    position: relative;
  }
  &__content {
    background-color: #1f1f1f;
    padding: 48px 0;
    color: #fff;
  }
  h2 {
    font-weight: 800;
  }
  display: block;
  width: 100%;
}

.chevron::before {
  border-style: solid;
  margin-top: 0.15em;
  border-color: #000;
  margin-left: 0.5em;
  border-width: 0.1em 0.1em 0 0;
  content: '';
  display: inline-block;
  height: 0.45em;
  left: 0.15em;
  position: relative;
  top: 0.15em;
  transform: rotate(-45deg);
  vertical-align: top;
  width: 0.45em;
}
.footer .chevron::before {
  border-color: #fff;
}

.chevron.right:before {
  left: 0;
  transform: rotate(45deg);
}
.copyright {
  margin-top: 100px;
  font-size: 12px;
  color: #a4a4a4;
}
.external__links {
  li {
    padding: 1em 0;
  }
  li + li {
    margin-left: 1em;
  }
}
.powered-by {
  margin-top: 1em;
  margin-bottom: 1em;
}
.triangle::after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);

  width: 0;
  height: 0;
  border-style: solid;
  border-width: 20px 25px 0 25px;
  z-index: 1;
}
.triangle--teal::after {
  border-color: $theme-teal transparent transparent transparent;
}
.triangle--grey::after {
  border-color: #f0f6f6 transparent transparent transparent;
}
.triangle--white::after {
  border-color: #fff transparent transparent transparent;
  bottom: initial !important;
  top: -50px !important;
  left: 100px !important;
}

.dialog {
  position: relative;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  margin: 1em;
  min-height: 300px;
  width: 400px;
  padding: 1em 1em 5em 1em;
  &__close {
    text-align: right;
    margin-left: auto;
    margin-bottom: 24px;
    button {
      background: #fff;
      border: 0;
      outline: 0;
      font-size: 2em;
    }
  }
  &__progress {
    top: -10px;
    left: 0;
    height: 10px;
    background: $theme-teal;
    position: absolute;
    z-index: 2;
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: -10px;
    width: attr(data-width);
    height: 10px;
    background: red;
    left: 0;
  }
  &__bordered {
    border-top: 10px #d5d5d5 solid;
  }

  &__step {
    padding: 1em 0 0.5em 0;
    font-weight: 800;
  }
  &__text {
    color: #656764;
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 1.5em;
  }
  &__title {
    margin-bottom: 1.5rem;
  }
}
.form {
  max-width: 470px;
  select {
    width: 30%;
    margin-left: 8px;
    background: #d5d5d5;
    outline: 0;
    border: 0;
    border-radius: 25px;
    padding: 1em;
  }
  .button {
    width: 100%;
    margin-top: 1em;

    font-weight: 800;
    min-width: initial !important;
  }
  &__input {
    & + .form__input {
      margin-top: 0.5em;

      p {
        color: red;
        font-size: 0.9rem;
      }
    }
