  }
  .social-icon {
    margin-left: 1em;
  }
}

#amp-burger {
  width: 100%;
  height: 45px;
  cursor: pointer;
  position: relative;
}

#amp-burger div.lines,
#amp-burger div.text {
  padding-left: 10px;
  display: inline-block;
}
#amp-burger div.text {
  position: absolute;
  top: 13px;
  left: 50px;
  z-index: 100;
  opacity: 1;
  cursor: pointer;
  font-weight: 800;
  color: #fff;
}
#amp-burger div.lines input {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 10000;
  opacity: 0;
  cursor: pointer;
}

#amp-burger div.lines-icon {
  position: relative;
  width: 50px;
  height: 50px;
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
}
#amp-burger div.lines-icon .icon-left {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  height: 4px;
  width: 15px;
  top: 20px;
  background-color: #000;
}
#amp-burger div.lines-icon .icon-left:before {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  width: 15px;
  height: 4px;
  background-color: #000;
  content: '';
  top: -10px;
}
#amp-burger div.lines-icon .icon-left:after {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  width: 15px;
  height: 4px;
  background-color: #000;
  content: '';
  top: 10px;
}
#amp-burger div.lines-icon .icon-left:hover {
  cursor: pointer;
}
#amp-burger div.lines-icon .icon-right {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  height: 4px;
  width: 15px;
  top: 20px;
  background-color: #000;
  left: 15px;
}
#amp-burger div.lines-icon .icon-right:before {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  width: 15px;
  height: 4px;
  background-color: #000;
  content: '';
  top: -10px;
}
#amp-burger div.lines-icon .icon-right:after {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  position: absolute;
  width: 15px;
  height: 4px;
  background-color: #000;
  content: '';
  top: 10px;
}

#amp-burger input:checked + div.lines-icon .icon-left {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  background: transparent;
}
#amp-burger input:checked + div.lines-icon .icon-left:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px);
  transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px);
}
#amp-burger input:checked + div.lines-icon .icon-left:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px);
  transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px);
}
#amp-burger input:checked + div.lines-icon .icon-right {
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  background: transparent;
}
#amp-burger input:checked + div.lines-icon .icon-right:before {
  -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px);
  transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px);
}
#amp-burger input:checked + div.lines-icon .icon-right:after {
  -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px);
  transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px);
}
.is--hidden {
  display: none;
}
.form select {
  width: 100% !important;
}

.button {
  font-weight: 600 !important;
}
.mobile-menu__wrapper {
  height: 100%;
  overflow: auto;
}

a {
  cursor: pointer;
}
.dialog {
  &__fullscreen {
    line-height: 1.6;
    position: fixed;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    top: 0 !important;
    width: 100%;
    overflow: auto;
    margin: 0 !important;
    transform: initial !important;

    .container {
      max-width: 769px;
    }
    .dialog__close {
      padding: 0 300px;
    }
    .pricing__description {
      text-align: left !important;
      margin: 0;
    }
  }
}
.pricing__content + .pricing__content {
  margin-top: 48px;
}
.mobile-menu {
  &__wrapper {
    z-index: 2;
    background: #fff;
    position: fixed;
    top: 75px;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
  }
}
.pricing {
  &__content {
    .pricing__description {
      max-width: 100%;
    }
    .grow-6 + .grow-6 {
      padding-left: 24px;
    }
    p {
      color: #4b4b4b;
    }
    > div,
    > p {
      margin: 16px !important;
    }
  }
  &__icon {
    margin-left: 15px;
    height: 16px;
  }
  &__sub-description {
    font-weight: 500;
    font-size: 18px;
    margin-top: 16px;
  }
  &__section {
    position: relative;
    color: #fff;
    min-height: 270px !important;
    background-color: $theme-teal;
  }
  line-height: 1.4;
  text-align: center;
  font-weight: 600;
  &__description {
    font-size: 24px;
    max-width: 250px;
    font-weight: 800;
    margin: auto;
  }
  &__price {
    font-size: 30px;
  }
  small {
    font-size: 16px;
  }
  &__detail {
    margin-top: 120px;
    padding-bottom: 24px;
  }
}

.pivoting-services {
  background-color: #f0f6f6;
  &:after {
    left: 50% !important;
    transform: translateX(-50%) !important;
    top: 0 !important;
  }
}
.dialog {
  .headline {
    font-size: 30px;
    margin: 16px;
    &:after {
      margin: initial;
    }
  }
}

#how-it-works__img {
  min-height: 350px;
  background-image: url('../images/How\ It\ Works\ min.png');
  background-repeat: no-repeat;
  display: block;
  margin: auto;
  background-position: center;
  background-size: contain;
}

@media screen and (max-width: 769px) {
  #placeholders {
    margin-top: 100px;
  }
  #how-it-works__img {
    background-image: url('../images/How\ It\ Works\ Mobile\ min.png');
    width: 100%;
    min-height: 800px;
  }
  .why-it-works__img {
    margin-top: 3em;
    width: 100%;
    height: initial !important;
  }
  .hero__img {
    width: 400px;
  }
  .dialog {
    margin: 0 !important;
  }
}

@media screen and (max-height: 500px) {
  .dialog {
    height: 100%;
    overflow: auto;
    min-height: initial;
  }
}

@media screen and (max-height: 300px) {
  .dialog {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    min-height: initial !important;
  }
}

@media screen and (max-width: 600px) {
  .dialog {
    width: 100% !important;
  }
}

