.dialog {
  background-color: white;
  padding-bottom: 24px;
  position: fixed;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  z-index: 11;
  border-radius: 10px;
}
.dialog__progress {
  transition: all 500ms;
}
.dialog__close {
  margin-bottom: 0;
}
.dialog__backdrop {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 10;
  position: fixed;
}
.input {
