ol.progtrckr {
  list-style-type: none;
  padding: 0;
}

ol.progtrckr li {
  display: inline-block;
  text-align: center;
  line-height: 4.2rem;
  cursor: pointer;
}

ol.progtrckr li span {
  padding: 0 1.5rem;
  visibility: hidden;
}

@media (max-width: 650px) {
  .progtrckr li span {
    display: none;
  }
}
.progtrckr em {
  display: none;
  font-weight: 700;
  padding-left: 1rem;
}

@media (max-width: 650px) {
  .progtrckr em {
    display: inline;
  }
}

ol.progtrckr li.progtrckr-todo {
  color: silver;
  border-bottom: 2px solid silver;
}

ol.progtrckr li.progtrckr-doing {
  color: black;
  border-bottom: 2px solid #cccccc;
}

ol.progtrckr li.progtrckr-done {
  color: black;
  border-bottom: 2px solid $theme-teal;
}

ol.progtrckr li:after {
  content: '\00a0\00a0';
}

ol.progtrckr li:before {
  position: relative;
  bottom: -3.7rem;
  float: left;
  left: 50%;
}

ol.progtrckr li.progtrckr-todo:before {
  content: '';
  color: white;
  background-color: $theme-teal;
  width: 1em;
  height: 1em;
  background-color: #fff;
  border: 2px solid $theme-teal;
  line-height: 1em;
  border-radius: 1em;
}

ol.progtrckr li.progtrckr-todo:hover:before {
  color: #ff4500;
}

ol.progtrckr li.progtrckr-doing:before {
  content: '';
  color: white;
  background-color: $theme-teal;
  width: 1em;
  height: 1em;
  background-color: #fff;
  border: 2px solid $theme-teal;
  line-height: 1em;
  border-radius: 1em;
}
