//   line-height: 1em;
//   border-radius: 1em;
// }

ol.progtrckr li.progtrckr-doing:hover:before {
  color: #ff4500;
}

ol.progtrckr li.progtrckr-done:before {
  content: '';
  height: 1em;
  border: 1px solid $theme-teal;
  color: white;
  background-color: $theme-teal;
  width: 1em;
  line-height: 1em;
  border-radius: 1em;
}

ol.progtrckr li.progtrckr-done:hover:before {
  color: #333;
}

